      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Telephone : ${this.state.director_contact} ` + ` ` + ` `+  ` `+ ` ` + ` `+ `Email Address : ${this.state.director_email} `,0.8, 8.5).setFontSize(14)
      doc.text(``, 0.5, 8.6).setFontSize(14)
      doc.text(`(C) Settlement Details [ Bank Account ]`, 0.5, 9.0).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Bank Name : ${this.state.bank_name}`, 0.8, 9.4).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Bank Branch : ${this.state.bank_branch}`, 0.8, 9.8).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Account Name : ${this.state.bank_account_name}`, 0.8, 10.2).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Account Number : ${this.state.bank_account_number}`, 0.8, 10.6).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`----------------------------------------------------------------------------------------------------------------------------------`, 0.5, 10.9).setFontSize(14)
      doc.text(``, 0.5, 6.0).setFontSize(12)
      doc.text(`Powered by Andston Solutions @${year}${separator}${month<10?`0${month}`:`${month}`}${separator}${date}`, 2.5, 11.2).setFontSize(10)
      // doc.addImage(this.state.gambar, 'JPEG', 0.5, 2, 2.9, 2.9)
