import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
//import Spinner from '../../Loaders/Spinner'
import './ModalStylee.css'
import Feedback from 'react-bootstrap/Feedback';
import Server_connections from '../../constants/constants';

class AddLandlordModal extends Component {
    constructor(props) {
      super(props);
      this.state = {
        business_name: '',
        property_name: '',
        district: '',
        county: '',
        postal_address: '',
        physical_address: '',
        numberOfSlots: '',
        reservedSlots: '',
        startTime: '',
        endTime: '',
        chargePerHour: '',
        office_contact: '',
        office_other_contact: '',
        email: '',
        director_surname: '',
        director_middle_name: '',
        director_other_name: '',
        director_gender: '',
        director_nationality: '',
        director_countryOfResidence: '',
        director_contact: '',
        director_email: '',
        bank_name: '',
        bank_branch: '',
        bank_account_name: '',
        bank_account_number: '',
        message: '',
        isLoaded: false,
        districts: [],
        counties: [],
        amounts: []
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleUpdateItem(e){
      e.preventDefault();

      const addNewLandlord = {
        business_name: this.state.business_name,
        property_name: this.state.property_name,
        district: this.state.district,
        county: this.state.county,
        postal_address: this.state.postal_address,
        physical_address: this.state.physical_address,
        numberOfSlots: this.state.numberOfSlots,
        reservedSlots: this.state.reservedSlots,
        startTime: this.state.startTime,
        endTime: this.state.endTime,
        chargePerHour: this.state.chargePerHour,
        office_contact: this.state.office_contact,
        office_other_contact: this.state.office_other_contact,
        email: this.state.email,
        director_surname: this.state.director_surname,
        director_middle_name: this.state.director_middle_name,
        director_other_name: this.state.director_other_name,
        director_gender: this.state.director_gender,
        director_nationality: this.state.director_nationality,
        director_countryOfResidence: this.state.director_countryOfResidence,
        director_contact: this.state.director_contact,
        director_email: this.state.director_email,
        bank_name: this.state.bank_name,
        bank_branch: this.state.bank_branch,
        bank_account_name: this.state.bank_account_name,
        bank_account_number: this.state.bank_account_number
      };

      console.log(addNewLandlord);

      fetch(Server_connections.ADD_NEW_LANDLORD_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(addNewLandlord)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });
    }

    componentDidMount(){
        fetch(Server_connections.DISTRICTS_URL)
        .then(res => res.json())
        .then(json => {
          this.setState({
            districts:  json.districts,
            isLoaded: true
          })
        });

        fetch(Server_connections.COUNTIES_URL)
        .then(res => res.json())
        .then(json => {
          this.setState({
            counties:  json.counties,
            isLoaded: true
          })
        });

        fetch(Server_connections.GET_ALL_CHARGES_URL)
        .then(res => res.json())
        .then(json => {
          this.setState({
            amounts:  json.amounts,
            isLoaded: true
          })
        })
    }

    render() {
      var { counties, districts, amounts} = this.state;
        return (
            <Modal
              {...this.props}
              size="lg"
              aria-labelledby="contained-modal-title-vcenter"
              centered
              id="exampleModal"
            >
              <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                  <div className="modal-title">Add New Landlord | <span className='modal-alert'>{this.state.message }</span></div>
                </Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <h4>{this.state.item_name}</h4>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="business_name" className="titles"> (A) Business Details</Label>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="business_name" className="lable">Business Name</Label>
                      <Input type="text" id="business_name" name="business_name" placeholder="Business Name" value ={this.state.business_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="property_name" className="lable">Property Name</Label>
                      <Input type="text" id="property_name" name="property_name" placeholder="Property Name" value ={this.state.property_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                <Col xs="4">
                    <FormGroup>
                        <Label htmlFor="district" className="lable">District</Label>
                        <Input type="select" name="district" value={this.state.district} onChange={this.onChange}>
                            <option></option>
                            {districts.map((district, index) => (
                                <option key={ index}>{ district.district_name}</option>
                            ))}
                        </Input>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                       <Label htmlFor="county" className="lable">Division</Label>
                       <Input type="select" name="county" value={this.state.county} onChange={this.onChange}>
                            <option></option>
                            {counties.map((county, index) => (
                                <option key={ index}>{ county.county_name}</option>
                            ))}
                        </Input>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="postal-address" className="lable">Postal Address</Label>
                      <Input type="text" name="postal_address" placeholder="Postal Address" value={this.state.postal_address} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="physical_address" className="lable">Physical Address</Label>
                      <Input type="text" name="physical_address" placeholder="Physical Address" value={this.state.physical_address} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="numberOfSlots" className="lable">Total Parking Slots</Label>
                      <Input type="number" name="numberOfSlots" placeholder="" value={this.state.numberOfSlots} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="reservedSlots" className="lable">Total Reserved Parking Slots</Label>
                      <Input type="number" name="reservedSlots" placeholder="" value={this.state.reservedSlots} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="startTime" className="lable">Open Time</Label>
                      <Input type="time" name="startTime" placeholder="" value={this.state.startTime} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="endTime" className="lable">Close Time</Label>
                      <Input type="time" name="endTime" placeholder="" value={this.state.endTime} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="chargePerHour" className="lable">Charge per hour</Label>
                      <Input type="select" name="chargePerHour" value={this.state.chargePerHour} onChange={this.onChange}>
                            <option></option>
                            {amounts.map((amount, index) => (
                                <option key={ index}>{ amount.amount}</option>
                            ))}
                        </Input>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="office_contact" className="lable">Office Telephone</Label>
                      <Input type="text" name="office_contact" placeholder="Telephone" value={this.state.office_contact} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="office_other_contact" className="lable">Other Telephone (Optional)</Label>
                      <Input type="text" name="office_other_contact" placeholder="Telephone" value={this.state.office_other_contact} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="email" className="lable">Email Address</Label>
                      <Input type="text" name="email" placeholder="Email Address" value={this.state.email} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <hr/>
                <FormGroup row className="my-0">
                  <Col xs="8">
                    <FormGroup>
                      <Label htmlFor="business_name" className="titles"> (B) Director 1: Owner Personal Identification</Label>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_surname" className="lable">Surname</Label>
                      <Input type="text" name="director_surname" placeholder="Surname" value={this.state.director_surname} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_middle_name" className="lable">Middle Name</Label>
                      <Input type="text" name="director_middle_name" placeholder="Middle name" value={this.state.director_middle_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_other_name" className="lable">Other Names</Label>
                      <Input type="text" name="director_other_name" placeholder="Other name" value={this.state.director_other_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_gender" className="lable">Gender</Label>
                      <Input type="select" name="director_gender" placeholder="Surname" value={this.state.director_gender} onChange={this.onChange}>
                      <option></option>
                      <option>Femal</option>
                      <option>Male</option>
                      </Input>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_nationality" className="lable">Nationality</Label>
                      <Input type="text" name="director_nationality" placeholder="Nationality" value={this.state.director_nationality} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="4">
                    <FormGroup>
                      <Label htmlFor="director_countryOfResidence" className="lable">Country Of Residence</Label>
                      <Input type="text" name="director_countryOfResidence" placeholder="Country" value={this.state.director_countryOfResidence} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="director_contact" className="lable">Telephone</Label>
                      <Input type="text" name="director_contact" placeholder="Telephone" value={this.state.director_contact} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="director_email" className="lable">Email Address</Label>
                      <Input type="text" name="director_email" placeholder="Email" value={this.state.director_email} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <hr/>
                <FormGroup row className="my-0">
                  <Col xs="8">
                    <FormGroup>
                      <Label htmlFor="business_name" className="titles"> (C) Settlement Details [ Bank Account ]</Label>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="bank_name" className="lable">Bank Name</Label>
                      <Input type="text" name="bank_name" placeholder="Bank Name" value={this.state.bank_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="bank_branch" className="lable">Bank Branch</Label>
                      <Input type="text" name="bank_branch" placeholder="Email" value={this.state.bank_branch} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="bank_account_name" className="lable">Account Name</Label>
                      <Input type="text" name="bank_account_name" placeholder="Account Name" value={this.state.bank_account_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="bank_account_number" className="lable">Account Number</Label>
                      <Input type="number" name="bank_account_number" placeholder="Account Number" value={this.state.bank_account_number} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <hr/>
                <span className='modal-alert'>{this.state.message }</span>
              </Modal.Body>
              <Modal.Footer>
                <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Cancle</Button>
                </div>
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Save</Button>
                </div>
              </Modal.Footer>
            </Modal>
          );
    }
}

export default AddLandlordModal;