import React, {Component} from 'react';
import { Route , withRouter} from 'react-router-dom';
import {
    Button,
    Badge,
    Table,
    Col, 
    Row, 
    Label
  } from 'reactstrap';
import DetailLandlordModal from './DetailLandlordModal'
// import EditModal from './EditModal'
import Spinner from '../Loaders/Spinner'
import '../Geographics/LocationStylee.css'
import Server_connections from '../../constants/constants';

class Parkings extends Component {
    constructor(props) {
        super(props);
        this.state = {
          landlords: [],
          isLoaded: false,
          searchItem: '',
          detailModalShow: false,
          editModalShow: false
        }
        this.replaceModalItem = this.replaceModalItem.bind(this);
    }

    replaceModalItem(index) {
      this.setState({
        requiredItem: index
      });
    }

    handleSearchInput = (e) => {
      this.setState({ searchItem: e.target.value})
    }

    componentDidMount(){
        fetch(Server_connections.LANDLORDS_URL)
        .then(res => res.json())
        .then(json => {
          this.setState({
            landlords:  json.landlords,
            isLoaded: true
          })
        })
    }

    render(){
      console.log(this.state.landlords);
      var { isLoaded, landlords } = this.state;

      let filteredItem = landlords.filter((landlords) => {
        return landlords.business_name.toLowerCase().includes(this.state.searchItem.toLowerCase());
      });

      let detailModalClose = () => this.setState({detailModalShow: false});
      let editModalClose = () => this.setState({editModalShow: false});

      const {landlordCode, businessNname, propertyName, District, County, postalAddress, physicalAddress, NumberOfSlots, 
        ReservedSlots, StartTime, EndTime, ChargePerHour, Office_contact, Office_other_contact, Email, CreatedOn, 
        SurName, MiddleName, OtherName, Gender, Nationality, Country, Telephon, DEmail, BankName, BankBranch,
        AccountName, AccountNumber} = this.state;

      if(!isLoaded){
        return <div className='spinner'>
          <Spinner/>
          </div>
      }
      else{
        return(
          <div>
              <div>
                  <label></label>
                  <form className="form-inline mr-auto formRight">
                      <input className="form-control mr-sm-2" type="text" placeholder="Search by Busines Name" aria-label="Search" 
                      onChange={this.handleSearchInput}/>
                      {/* <button className="btn-pill btn-success" type="submit">Search</button> */}
                  </form>
              </div>
              <hr/>
              <div className="parentDiv">
              <Table hover responsive className="table-outline mb-0 d-none d-sm-table">
                <thead className="thead-light">
                <tr>
                  {/* <th className="text-center"><i className="icon-picture"></i></th> */}
                  <th>Landlord Ref</th>
                  <th>Business Name</th>
                  <th>Property Name</th>
                  <th>Parking Slots</th>
                  <th>Amount (Ugx /hr)</th>
                  {/* <th>Location</th> */}
                  <th>Action</th>
                </tr>
                </thead>
                <tbody>
                {filteredItem.map((landlord, index) => (
                <tr key={ index }>
                  {/* <td className="text-center">
                    <div className="avatar">
                      <img src={item.item_photo} className="img-avatar" alt="admin@bootstrapmaster.com" />
                      <span className="avatar-status badge-success"></span>
                    </div>
                  </td> */}
                  <td>
                    <div>{landlord.landlord_code}</div>
                    {/* <div className="small text-muted">
                      Registered: {landlord.created_at}
                    </div> */}
                  </td>
                  <td className="">
                    <div>{landlord.business_name}</div>
                  </td>
                  <td className="">
                    <div>{landlord.property_name}</div>
                  </td>
                  <td className="">
                    <div>{landlord.numberOfSlots}</div>
                  </td>
                  <td className="">
                    <div>{landlord.chargePerHour}</div>
                  </td>
                  {/* <td className="">
                    <div>{landlord.county}</div>
                  </td> */}
                  <td>
                  <Label block color="info" size="sm" className="btn-pill bg-info" 
                      onClick={() => {
                        this.setState({detailModalShow: true,
                          landlordCode: landlord.landlord_code, businessNname: landlord.business_name, propertyName: landlord.property_name,
                          District: landlord.district, County: landlord.county, postalAddress: landlord.postal_address, physicalAddress: landlord.physical_address,
                          NumberOfSlots: landlord.numberOfSlots, ReservedSlots: landlord.reservedSlots, StartTime: landlord.startTime,
                          EndTime: landlord.endTime, ChargePerHour: landlord.chargePerHour, Office_contact: landlord.office_contact, 
                          Office_other_contact: landlord.office_other_contact, Email: landlord.email, CreatedOn: landlord.created_at,
                          SurName: landlord.director_surname, MiddleName: landlord.director_middle_name, OtherName: landlord.director_other_name,
                          Gender: landlord.director_gender, Nationality: landlord.director_nationality, Country: landlord.director_countryOfResidence,
                          Telephon: landlord.director_contact, DEmail: landlord.director_email, BankName: landlord.bank_name,
                          BankBranch: landlord.bank_branch, AccountName: landlord.bank_account_name, AccountNumber: landlord.bank_account_number});
                        }}><strong className="action">Details</strong></Label>
                  </td>
                </tr>
                ))}
                </tbody>
              </Table>
              
              <DetailLandlordModal 
                landlord_code = {landlordCode}
                business_name = {businessNname}
                property_name = {propertyName}
                district = {District}
                county = {County}
                postal_address = {postalAddress}
                physical_address = {physicalAddress}
                numberOfSlots = {NumberOfSlots}
                reservedSlots = {ReservedSlots}
                startTime = {StartTime}
                endTime = {EndTime}
                chargePerHour = {ChargePerHour}
                office_contact = {Office_contact}
                office_other_contact = {Office_other_contact}
                email = {Email}
                created_at = {CreatedOn}
                director_surname = {SurName}
                director_middle_name = {MiddleName}
                director_other_name = {OtherName}
                director_gender = {Gender}
                director_nationality = {Nationality}
                director_countryOfResidence = {Country}
                director_contact = {Telephon}
                director_email = {DEmail}
                bank_name = {BankName}
                bank_branch = {BankBranch}
                bank_account_name = {AccountName}
                bank_account_number = {AccountNumber}

                show= { this.state.detailModalShow }
                onHide= { detailModalClose }
              />
          </div>
          </div>
      );
      }
    }
}

export default withRouter(Parkings);